import Vue from 'vue'
// import { BootstrapVue } from 'bootstrap-vue'
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap"
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Dashboard from '../components/Dashboard.vue'
import Login from '../components/pages/Login.vue'
import Products from '../components/pages/Products.vue'
import Coupons from '../components/pages/Coupons.vue'
import OrdersCheck from '../components/pages/OrdersCheck.vue'
import CustomerOrder from '../components/pages/CustomerOrders.vue'
import CustomerCheckout from '@/components/pages/CustomerCheckout.vue';
import Shop from '../components/Shop.vue'
import Product from '../components/pages/Product.vue'
import CheckOut1 from '../components/pages/CheckOut1.vue'
import Purchase from '../components/pages/Purchase.vue'
import axios from 'axios'

const http = axios.create({
  baseURL: '',
})


Vue.use(VueRouter)
// Vue.use(BootstrapVue)

const routes = [
 
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/',
    name: 'Shop',
    component: Shop, 
    children:[
      {
        path: '/',
        name: 'Product',
        component: Product
      },
      {
        path: '/checkout1',
        name: 'CheckOut1',
        component: CheckOut1,
      },
      {
        path: '/purchase/:orderId',
        name: 'Purchase',
        component: Purchase,
      },
    ]
  }, 
  
  {
    path: '/admin',
    name: 'Dashboard',
    component: Dashboard, 
    children:[
      {
        path: 'products',
        name: 'Products',
        component: Products,
        meta: { requiresAuth: true }
      },
       {
        path: 'coupons',
        name: 'Coupons',
        component: Coupons,
        meta: { requiresAuth: true }
      },
      {
       path: 'orderscheck',
       name: 'OrdersCheck',
       component: OrdersCheck,
       meta: { requiresAuth: true }
     },
    ]
  }, 
  {
    path: 'dashboard',
    name: 'Dashboard',
    component: Dashboard, 
    children:[
      {
        path: 'customer_order',
        name: 'customer_order',
        component: CustomerOrder,
      },
      {
        path: 'customer_checkout/:orderId',
        name: 'CustomerCheckout',
        component: CustomerCheckout,
      },
    ]
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  console.log(to, from, next);
  // ...
  if (to.meta.requiresAuth) {
    const api = `${process.env.VUE_APP_APIPATH}/api/user/check`; // 'http://localhost:3000/api/casper/products';
    http.post(api).then((response) => {
        console.log(response.data);
        if (response.data.success) {
    next();
        }else{
          console.log('這裡需要驗證');
          this.$router.push(`/`)
        }
    });
  }else{
    next();
  }
})

export default router
