<template>
  <div>
    <div class="p-5 mb-4 bg-light rounded-3">
      <div class="container-fluid py-5">
        <h1 class="display-5 fw-bold">買到剁手手！最後出清</h1>
        <p class="fs-4">
          This is a modified jumbotron that occupies the entire horizontal space
          of its parent.
        </p>
      </div>
    </div>

    <div class="container">
      <div class="row">
        <div class="col-md-2">
          <div class="mt-4 sticky-top">
            <ul class="list-group">
              <li class="list-group-item" @click.prevent="getProducts">
                <a href=""> 全部商品</a>
              </li>

              <li
                class="list-group-item"
                value="禮品區"
                @click.prevent="getGift"
              >
                <a href="">禮品區</a>
              </li>

              <li
                class="list-group-item"
                value="迷因專區"
                @click.prevent="getMeme"
              >
                <a href="">迷因專區</a>
              </li>

              <li
                class="list-group-item"
                value="寵物用品"
                @click.prevent="getPet"
              >
                <a href="">寵物用品</a>
              </li>

              <!-- search -->
              <!-- <li class="list-group-item p-0">
                <form class="form-inline">
                  <input
                    class="form-control w-75 pl-3 rounded-0"
                    type="search"
                    placeholder="搜尋"
                    aria-label="Search"
                    v-model="searchWord"
                    @keyup.enter="searchItem"
                  />
                  <button
                    class="btn btn-outline-primary w-25 rounded-0 px-0"
                    type="submit"
                    @click.prevent="searchItem"
                  >
                    <i class="fa fa-search" aria-hidden="true"></i>
                  </button>
                </form>
              </li> -->
            </ul>
          </div>
        </div>
        <div class="col-md-10">
          <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
              <div class="card border-0 shadow-sm">
                <div
                  style="
                    height: 150px;
                    background-size: cover;
                    background-position: center;
                  "
                  :style="{ backgroundImage: `url(${item.imageUrl})` }"
                ></div>
                <div class="card-body">
                  <span class="badge badge-secondary float-right ml-2">{{
                    item.category
                  }}</span>
                  <h5 class="card-title">
                    <a href="#" class="text-dark">{{ item.title }}</a>
                  </h5>
                  <p class="card-text">{{ item.content }}</p>
                  <div
                    class="d-flex justify-content-between align-items-baseline"
                  >
                    <div class="h5" v-if="!item.price">
                      {{ item.origin_price }} 元
                    </div>
                    <del class="h6" v-if="item.price"
                      >原價 {{ item.origin_price }} 元</del
                    >
                    <div class="h5" v-if="item.price">
                      現在只要 {{ item.price }} 元
                    </div>
                  </div>
                </div>
                <div class="card-footer d-flex">
                  <button
                    type="button"
                    class="btn btn-outline-secondary btn-sm"
                    @click="getProduct(item.id)"
                  >
                    <i
                      class="fas fa-spinner fa-spin"
                      v-if="status.loadingItem === item.id"
                    ></i>
                    查看更多
                  </button>
                  <button
                    type="button"
                    class="btn btn-outline-danger btn-sm ml-auto"
                    @click="addtoCart(item.id)"
                  >
                    <i
                      class="fas fa-spinner fa-spin"
                      v-if="status.loadingItem === item.id"
                    ></i>
                    加到購物車
                  </button>
                </div>
              </div>
            </div>
          </div>
          <!-- pagination -->
          <nav aria-label="Page navigation example" v-if="pagination">
            <ul class="pagination">
              <li class="page-item" :class="{ disabled: !pagination.has_pre }">
                <a
                  class="page-link"
                  href="#"
                  aria-label="Previous"
                  @click.prevent="getProducts(pagination.current_page - 1)"
                >
                  <span aria-hidden="true">&laquo;</span>
                </a>
              </li>
              <li
                class="page-item"
                v-for="page in pagination.total_pages"
                :key="page"
                :class="{ active: pagination.current_page === page }"
              >
                <a
                  class="page-link"
                  href="#"
                  @click.prevent="getProducts(page)"
                  >{{ page }}</a
                >
              </li>
              <li class="page-item" :class="{ disabled: !pagination.has_next }">
                <a
                  class="page-link"
                  href="#"
                  aria-label="Next"
                  @click.prevent="getProducts(pagination.current_page + 1)"
                >
                  <span aria-hidden="true">&raquo;</span>
                </a>
              </li>
            </ul>
          </nav>
        </div>

        <!-- 購物車 -->
        <div class="cart-dropdown">
          <button
            class="btn btn-sm btn-cart"
            type="button"
            id="dropdownMenuButton"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            <i
              class="fa fa-shopping-cart text-secondary fa-3x shoppingcart"
            ></i>
            <span class="badge badge-pill badge-danger">{{
              cart.carts.length
            }}</span>
            <!-- <p class="my-0 text-secondary">購物車</p> -->
          </button>
          <div
            class="dropdown-menu dropdown-menu-right"
            style="min-width: 300px"
            aria-labelledby="dropdownMenuButton"
          >
            <div class="px-4 px-3">
              <h6>已選擇商品</h6>
              <!-- <div class="my-5 row justify-content-center"> -->
              <!-- <div class="col-md-6"> -->
              <table class="table">
                <tbody>
                  <tr v-for="item in cart.carts" :key="item.id">
                    <td class="align-middle">
                      <button
                        type="button"
                        class="btn btn-outline-danger btn-sm"
                        @click="removeCartItem(item.id)"
                      >
                        <i class="fa fa-trash" aria-hidden="true"></i>
                      </button>
                    </td>
                    <td class="align-middle">
                      {{ item.product.title }}
                      <div class="text-success" v-if="item.coupon">
                        已套用優惠券
                      </div>
                    </td>
                    <td class="align-middle">
                      {{ item.qty }}/{{ item.product.unit }}
                    </td>
                    <td class="align-middle text-right">
                      {{ item.final_total }}
                    </td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td colspan="3" class="text-right">總計</td>
                    <td class="text-right">{{ cart.total }}</td>
                  </tr>
                  <tr v-if="cart.final_total !== cart.total">
                    <td colspan="3" class="text-right text-success">折扣價</td>
                    <td class="text-right text-success">
                      {{ cart.final_total }}
                    </td>
                  </tr>
                </tfoot>
              </table>
            </div>
            <!-- </div> -->
            <a
              href="#"
              class="btn btn-primary btn-block"
              @click.prevent="checkout_page"
              >結帳去</a
            >
            <!-- </div> -->
          </div>
        </div>
      </div>
    </div>

    <!-- model -->
    <div
      ref="productModal"
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              {{ product.title }}
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>

          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="" />
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">
                {{ product.description }}
              </footer>
            </blockquote>
            <div class="d-fiex justify-content-between align-item-baseline">
              <div class="h4" v-if="!product.price">
                {{ product.origin_price }} 元
              </div>
              <del class="h6" v-if="product.price"
                >原價 {{ product.origin_price }} 元</del
              >
              <div class="h4" v-if="product.price">
                現在只要 {{ product.price }} 元
              </div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">
                選購 {{ num }} {{ product.unit }}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計 <strong>{{ product.num * product.price }}</strong> 元
            </div>
            <button
              type="button"
              class="btn btn-primary"
              @click="addtoCart(product.id, product.num)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="product.id === status.loadingItem"
              ></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <footer class="text-muted py-5">
      <div class="container">
        <p class="float-end mb-1">
          <a href="#">Back to top</a>
        </p>
        <p class="mb-1">
          Album example is &copy; Bootstrap, but please download and customize
          it for yourself!
        </p>
        <p class="mb-0">
          New to Bootstrap? <a href="/">Visit the homepage</a> or read our
          <a href="/docs/5.1/getting-started/introduction/"
            >getting started guide</a
          >.
        </p>
      </div>
    </footer>
  </div>
</template>

<style lang="scss">
.cart-dropdown {
  position: fixed;
  bottom: 10%;
  width: 40px;
  right: 8%;
  z-index: 99;
}
.badge-pill {
  position: absolute;
  bottom: 0%;
  right: 0%;
}
</style>


<script>
import { Modal } from "bootstrap";

export default {
  data() {
    return {
      products: [],
      pagination: {},
      product: {},
      isLoading: false,
      status: {
        loadingItem: "",
      },
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
      cart: {},
    };
  },
  computed: {
    myModal() {
      return new Modal(this.$refs.productModal);
    },
    nameIsValid() {
      return !!this.form.user.name;
    },
    emailIsValid() {
      return !!this.form.user.email;
    },
    telIsValid() {
      return typeof this.form.user.tel === "number";
    },
    addressIsValid() {
      return !!this.form.user.address;
    },
    formIsValid() {
      return (
        this.nameIsValid &&
        this.emailIsValid &&
        this.telIsValid &&
        this.addressIsValid
      );
    },
    // delModal() {
    //   return new Modal(this.$refs.delProductModal);
    // },
  },
  methods: {
    submitform() {
      if (this.formIsValid) {
        console.log("Formsubmitted", this.form);
      } else {
        console.log("Invalid form");
      }
    },
    getProducts(page = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products?page=${page}`;

      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.products = response.data.products;
        console.log(response);
        vm.isLoading = false;
      });
    },
    getGift() {
      this.pagination = false;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.products = response.data.products.map(function (item) {
          if (item.category === "禮品") {
            return item;
          }
          return false;
        });
        vm.products = vm.products.filter(function (item) {
          return item.is_enabled === 1;
        });
        console.log("禮品", vm.products);
        vm.isLoading = false;
        // vm.pagination = response.data.pagination;
      });
    },
    getMeme() {
      this.pagination = false;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.products = response.data.products.map(function (item) {
          if (item.category === "迷因") {
            return item;
          }
          return false;
        });
        vm.products = vm.products.filter(function (item) {
          return item.is_enabled === 1;
        });
        console.log("迷因", vm.products);
        vm.isLoading = false;
        // vm.pagination = response.data.pagination;
      });
    },
    getPet() {
      this.pagination = false;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.products = response.data.products.map(function (item) {
          if (item.category === "寵物用品") {
            return item;
          }
          return false;
        });
        vm.products = vm.products.filter(function (item) {
          return item.is_enabled === 1;
        });
        console.log("寵物用品", vm.products);
        vm.isLoading = false;
        // vm.pagination = response.data.pagination;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      const token = localStorage.getItem("token");
      vm.status.loadingItem = id;
      this.$http
        .get(url, {
          headers: { Authorization: token },
        })
        .then((response) => {
          vm.product = response.data.product;
          this.myModal.show();
          console.log(response);
          vm.status.loadingItem = "";
        });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      const token = localStorage.getItem("token");
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http
        .post(url, { data: cart }, { headers: { Authorization: token } })
        .then((response) => {
          console.log(response);
          vm.status.loadingItem = "";
          vm.getCart();
          this.myModal.hide();
        });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      const token = localStorage.getItem("token");
      vm.isLoading = true;
      this.$http
        .get(url, { headers: { Authorization: token } })
        .then((response) => {
          vm.cart = response.data.data;
          console.log(response);
          vm.isLoading = false;
        });
    },
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      const token = localStorage.getItem("token");
      vm.isLoading = true;
      this.$http.delete(url, { headers: { Authorization: token } }).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },
    createOrder() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
      const order = vm.form;
      const token = localStorage.getItem("token");
      vm.isLoading = true;
      this.$http
        .post(url, { data: order }, { headers: { Authorization: token } })
        .then((response) => {
          console.log("訂單已建立", response);
          if (response.data.success) {
            vm.$router.push(`/customer_checkout/${response.data.orderId}`);
          }
          vm.isLoading = false;
        });
    },
    checkout_page() {
      this.$router.push(`/checkout1`);
    },
  },
  created() {
    this.getProducts();
    this.getCart();
  },
};
</script>