<template>
  <div>
    <div class="container">
      <div class="my-3 form-row text-center">
        <div class="col-12 col-sm">
          <div class="alert alert-primary alert-rounded" role="alert">
            1.輸入訂單資料
          </div>
        </div>
        <div class="col-12 col-sm">
          <div class="alert alert-light alert-rounded" role="alert">
            2.金流付款
          </div>
        </div>
        <div class="col-12 col-sm">
          <div class="alert alert-light alert-rounded" role="alert">3.完成</div>
        </div>
      </div>
    </div>

    <!-- cart -->
    <div class="my-5 row justify-content-center">
      <div class="col-md-6 my-5 row justify-content-center">
        <table class="table">
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <!-- v-if="cart.carts" -->
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <!-- <div class="text-success" v-if="item.coupon">
                  已套用優惠券
                </div> -->
              </td>
              <td class="align-middle">
                {{ item.qty }}/{{ item.product.unit }}
              </td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total }}</td>
            </tr>
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-success">折扣價</td>
              <td class="text-success">{{ cart.final_total }}</td>
            </tr>
          </tfoot>
        </table>

        <!-- 優惠碼 -->
        <!-- <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
              套用優惠碼
            </button>
          </div>
        </div> -->
      </div>
    </div>

    <!-- 表單 -->
    <div class="my-5 row justify-content-center">
      <form class="col-md-6" @submit.prevent="createOrder">
        <div class="form-group">
          <label for="useremail">Email</label>
          <input
            type="email"
            class="form-control"
            name="email"
            id="useremail"
            v-model="form.user.email"
            placeholder="請輸入 Email"
          />
          <p v-if="!emailIsValid" class="text-danger">Email欄位不得留空</p>
        </div>

        <div class="form-group">
          <label for="username">收件人姓名</label>

          <input
            type="text"
            class="form-control"
            name="name"
            id="username"
            v-model="form.user.name"
            placeholder="輸入姓名"
          />
          <p v-if="!nameIsValid" class="text-danger">姓名欄位不得留空</p>
        </div>

        <div class="form-group">
          <label for="usertel">收件人電話</label>
          <input
            type="tel"
            class="form-control"
            id="usertel"
            v-model.number="form.user.tel"
            placeholder="請輸入電話"
          />
          <p v-if="!telIsValid" class="text-danger">電話欄位不得留空</p>
        </div>

        <div class="form-group">
          <label for="useraddress">收件人地址</label>
          <input
            type="address"
            class="form-control"
            name="address"
            id="useraddress"
            v-model="form.user.address"
            placeholder="請輸入地址"
          />
          <p v-if="!addressIsValid" class="text-danger">地址欄位不得留空</p>
        </div>

        <div class="form-group">
          <label for="useraddress">留言</label>
          <textarea
            name=""
            id=""
            class="form-control"
            cols="30"
            rows="10"
            v-model="form.message"
          ></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger" :disabled="!formIsValid">
            送出訂單
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { Modal } from "bootstrap";

export default {
  data() {
    return {
      products: [],
      product: {},
      isLoading: false,
      status: {
        loadingItem: "",
      },
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
      cart: {},
    };
  },
  computed: {
    myModal() {
      return new Modal(this.$refs.productModal);
    },
    nameIsValid() {
      return !!this.form.user.name;
    },
    emailIsValid() {
      return !!this.form.user.email;
    },
    telIsValid() {
      return typeof this.form.user.tel === "number";
    },
    addressIsValid() {
      return !!this.form.user.address;
    },
    formIsValid() {
      return (
        this.nameIsValid &&
        this.emailIsValid &&
        this.telIsValid &&
        this.addressIsValid
      );
    },
    // delModal() {
    //   return new Modal(this.$refs.delProductModal);
    // },
  },
  methods: {
    submitform() {
      if (this.formIsValid) {
        console.log("Formsubmitted", this.form);
      } else {
        console.log("Invalid form");
      }
    },
    getProducts() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products`;

      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.products = response.data.products;
        console.log(response);
        vm.isLoading = false;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      const token = localStorage.getItem("token");
      vm.status.loadingItem = id;
      this.$http
        .get(url, {
          headers: { Authorization: token },
        })
        .then((response) => {
          vm.product = response.data.product;
          this.myModal.show();
          console.log(response);
          vm.status.loadingItem = "";
        });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      const token = localStorage.getItem("token");
      vm.isLoading = true;
      this.$http
        .get(url, { headers: { Authorization: token } })
        .then((response) => {
          vm.cart = response.data.data;
          console.log(response);
          vm.isLoading = false;
        });
    },
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      const token = localStorage.getItem("token");
      vm.isLoading = true;
      this.$http.delete(url, { headers: { Authorization: token } }).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },
    createOrder() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
      const order = vm.form;
      const token = localStorage.getItem("token");
      vm.isLoading = true;
      this.$http
        .post(url, { data: order }, { headers: { Authorization: token } })
        .then((response) => {
          console.log("訂單已建立", response);
          if (response.data.success) {
            vm.$router.push(`/purchase/${response.data.orderId}`);
          }
          vm.isLoading = false;
        });
    },
  },
  created() {
    this.getProducts();
    this.getCart();
  },
};
</script>